import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,Platform } from 'ionic-angular';
 import { ViewChild } from '@angular/core';
 import { ImageViewerController } from 'ionic-img-viewer';
/**
 * Generated class for the LintelPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-lintel',
  templateUrl: 'lintel.html',
})
export class LintelPage {

 @ViewChild('mySlider') mySlider: any;
_imageViewerCtrl: ImageViewerController;
  constructor(public navCtrl: NavController, public navParams: NavParams, imageViewerCtrl: ImageViewerController, public platform: Platform) {
  this._imageViewerCtrl = imageViewerCtrl;
   let backAction =  platform.registerBackButtonAction(() => {
        console.log("lintel");
        this.navCtrl.pop();
        backAction();
      },2)
  
  }
  
   presentImage(myImage) {
   
    const imageViewer = this._imageViewerCtrl.create(myImage);
    imageViewer.present();
    
   
    
    
  }
  
  elenco =  [
             {
               "img": "assets/img/qaprofil/lintel/1.png",
               "posicao": "Lintel",
               "nome": "QSGLNTL"
               
             },
             {
               "img": "assets/img/qaprofil/lintel/2.png",
                "posicao": "Lintel",
               "nome": "QSGLNTL"
               
             },
             {
               "img": "assets/img/qaprofil/lintel/3.png",
               "posicao": "Lintel",
               "nome": "QSGLNTL"
             },
             {
               "img": "assets/img/qaprofil/lintel/4.bmp",
               "posicao": "Lintel",
               "nome": "QSGLNTL"
             } 
           ]
           
           
           slideNext(){
       this.mySlider.slideNext();
     }
 
     slidePrev(){
       this.mySlider.slidePrev();
     }
  
  
  

  ionViewDidLoad() {
    console.log('ionViewDidLoad LintelPage');
  }
  
     startApp(page) {
    this.navCtrl.setRoot(page, {}, {
      animate: true,
      direction: 'forward'
    });
  
}

}



